<template>
<div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
  <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <template>
      <el-button @click="dialogVisible=true"></el-button>
      <el-dialog
  title=""
  :visible.sync="dialogVisible"
  width="30%"
  @close="handleClose">
 <el-form :model="user" :rules="rules" ref="addform" label-width="100px" class="demo-ruleForm">
  <el-form-item label="" prop="username">
    <el-input v-model="user.username"></el-input>
  </el-form-item>
  <el-form-item label="" prop="password">
    <el-input v-model="user.password"></el-input>
  </el-form-item>
  <el-form-item label="" prop="email">
    <el-input v-model="user.email"></el-input>
  </el-form-item>
  <el-form-item label="" prop="mobile">
    <el-input v-model="user.mobile"></el-input>
  </el-form-item>
 </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="primary" @click="adduser"> </el-button>
  </span>
</el-dialog>
  <el-table
    :data="tableData.filter(data => !search || data.username.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%">

    <el-table-column
      label=""
      prop="username">
    </el-table-column>
    <el-table-column
      label=""
      prop="email">
    </el-table-column>
    <el-table-column
      label=""
      prop="mobile">
    </el-table-column>
    <el-table-column
      label=""
      prop="role_name">
    </el-table-column>
    <el-table-column
      label="">
      <template slot-scope="scope">
    <el-switch
      v-model="scope.row.mg_state"      

      active-color="#13ce66"
      inactive-color="#ff4949"  @change="statechange(scope.row)">
    </el-switch>
      </template>
      
    </el-table-column>
    <el-table-column
      align="left" width="200px">
      <template slot="header" slot-scope="scope">
        <el-input
          v-model="search"
          size="mini"
          placeholder="" :m="scope"/>
      </template>
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.row.id)"><i class="el-icon-edit"></i>
          </el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete( scope.row.id)"><i class="el-icon-delete"></i></el-button>

          <el-button
          size="mini"
          @click="handleConfig(scope.row)"><i class="el-icon-setting"></i></el-button>
      </template>
    </el-table-column>
  </el-table>
  <div class="block">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="queryInfo.pagenum"
      :page-size="queryInfo.pagesize"
      layout="prev, pager, next, jumper"
      :total=total>
    </el-pagination>
  </div>

<el-dialog
  title=""
  :visible.sync="editVisible"
  width="30%"
  @close="editclose"
>
<el-form :model="editform" :rules="rules" ref="editForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="" prop="username">
    <el-input v-model="editform.username" disabled></el-input>
  </el-form-item>
    <el-form-item label="" prop="email">
    <el-input v-model="editform.email"></el-input>
  </el-form-item>
    <el-form-item label="" prop="mobile">
    <el-input v-model="editform.mobile"></el-input>
  </el-form-item>
</el-form> 
  <span slot="footer" class="dialog-footer">
    <el-button @click="editVisible = false"> </el-button>
    <el-button type="primary" @click="edituser()"> </el-button>
  </span>
</el-dialog>

<el-dialog
  title=""
  :visible.sync="userroleVisible"
  width="30%"
>
<div>
  <p>{{userrole.username}}</p>
  <p>{{userrole.role_name}}</p>
  <p>  
    <el-select v-model="role" placeholder="">
    <el-option
      v-for="item in roles"
      :key="item.id"
      :label="item.roleName"
      :value="item.id">
    </el-option>
  </el-select>
  </p>
    <span slot="footer" class="dialog-footer">
    <el-button @click="userroleVisible = false"> </el-button>
    <el-button type="primary" @click="edituserrole()"> </el-button>
  </span>


</div>
</el-dialog>

  </template>
</div>
</template>

<script>

export default {
    data(){
        return{
         tableData: [],
        search: '',
        queryInfo:{
        query:'',
        pagenum:1,
        pagesize:3
        },
        total:0,
        dialogVisible: false,
        editVisible:false,
        userroleVisible:false,
        userrole:{},
        role:'',
        roles:{},
        editform:{
        },
        user:{
          username:'',
          password:'',
          email:'',
          mobile:''
        },
        rules:{
          username: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '', trigger: 'blur' },
            {min:3,max:10,message:'310',trigger:'blur'}
          ],
          email: [
            { type: 'email', required: true, message: '', trigger: 'blur' }
          ],
          mobile: [
            {  required: true, message: '', trigger: 'blur' }
          ]
        
        }

      }

    },
    created(){
        this.getuserlist();


    },
    methods: {
    async getuserlist(){
        const {data:d}=await this.$http.get('users',{params:this.queryInfo});
            if(d.meta.status!=200){
                return this.$message("")
            }
          console.log(d)
        this.tableData=d.data.users;
        this.total=d.data.total;
    },
    async statechange(user){
      let {data:d}=await this.$http.put(`users/${user.id}/state/${user.mg_state}`)
      if(d.meta.status!=200){
        user.mg_state=!user.mg_state;
        return this.$message.error('')
      }
      return this.$message.success("")

    },
    adduser(){
      this.$refs.addform.validate(
        async (valid)=>{
          if(valid) {
            const {data:d}= await this.$http.post('users',this.user);
            console.log(d)
            if(d.meta.status!=201){
              return this.$message.error('');
            }
            this.$message.success('');
            this.dialogVisible=false;
            this.getuserlist()
            
          }

        }
      )

    },
    handleClose() {
      this.$refs['addform'].resetFields();
      },
    editclose(){
      this.$refs.editForm.resetFields()

    },
    async handleEdit(id) {

        let {data:d}=await this.$http.get(`users/${id}`);
        this.editform=d.data;
        this.editVisible=true;

      },
      async handleDelete(id) {
        const reslut=await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });      
          });
        if(reslut=='confirm'){
          const {data:d}=await this.$http.delete(`users/${id}`)
          if(d.meta.status!=200){
            return this.$message.error('')
          }
          this.$message.success('')
          this.getuserlist()
        }
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        this.queryInfo.pagenum=val;
        console.log(this.queryInfo.pagenum);
        this.getuserlist()
      }
    ,
    edituser(){
      this.$refs.editForm.validate(
        async valid=>{
          if(valid){
            const {data:d}= await this.$http.put(`users/${this.editform.id}`,{email:this.editform.email,mobile:this.editform.mobile});
            if(d.meta.status!=200){
              return this.$message.erro('');
            }
            this.$message.success('')
            this.editVisible=false;
            this.getuserlist();
          }
        }
      )

    },
    async getuserroles(){
      const {data:d}=await this.$http.get('roles');
      if(d.meta.status!=200){
        return this.$message.error('')
      }
      this.roles=d.data;
      console.log(this.roles)

    },
    handleConfig(item){
      this.userrole=item;
      this.getuserroles();
      this.userroleVisible=true;

    },
    async edituserrole(){
      console.log(this.role);
      if(!this.role){
        return this.$message.error('')
      }
      const {data:d}=await this.$http.put(`users/${this.userrole.id}/role`,{rid:this.role});
      console.log(d)
      if(d.meta.status!=200){
        return this.$message.error('')
      }
      this.$message.success('')
      this.userroleVisible=false;
      this.getuserlist();

    }
    },

    
}
</script>